___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: 'Arial', sans-serif;
    background-color: #393e46; /* tertiary */
    margin: 0;
  }
  
  .jumbotron {
    color: #FFD369; /* secondary */
    text-align: center;
    font-size: 36px;
    padding: 2rem;
    margin-bottom: 36px;
    width: 100%;
    background-color: #222831;
  }

  .button {
    background-color: #FFD369; /* tertiary */
    color: black;
    border: 0;
    padding: 14px 62px;
    border-radius: 4px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin-bottom: 36px;
    transition-duration: 0.2s;
    border: 1px transparent;
    cursor: pointer;
  }

  .button:hover {
    background-color: #393e46; /* tertiary */
    border: 1px solid #FFD369; /* tertiary */
    color: white;
  }

  .row {
    justify-content: center;
  }

  body .container .cardd {
    position: relative;
    min-width: 240px;
    height: 340px;
    box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
      inset -5px -5px 15px rgba(255, 255, 255, 0.1),
      5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    margin: 15px;
    transition: 0.5s;
  }
  
  body .container .cardd:nth-child(1) .box .content a {
    background: #FFD369;
  }
  
  body .container .cardd:nth-child(2) .box .content a {
    background: #FFD369;
  }
  
  body .container .cardd:nth-child(3) .box .content a {
    background: #FFD369;
  }
  
  body .container .cardd:nth-child(4) .box .content a {
    background: #FFD369;
  }
  
  body .container .cardd .box {
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    background: #2a2b2f;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: 0.5s;
  }
  
  body .container .cardd .box:hover {
    transform: translateY(-30px);
  }
  
  body .container .cardd .box:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: rgba(255, 255, 255, 0.03);
  }
  
  body .container .cardd .box .content {
    padding: 20px;
    text-align: center;
  }
  
  body .container .cardd .box .content h2 {
    position: absolute;
    top: -5px;
    right: 15px;
    font-size: 8rem; /* Increased font-size */
    color: rgba(255, 255, 255, 0.1);
  }
  
  body .container .cardd .box .content h3 {
    font-size: 1.4rem;
    color: #fff;
    z-index: 1;
    transition: 0.5s;
    margin-bottom: 10px;
  }
  
  body .container .cardd .box .content p {
    font-size: 1rem; /* Increased font-size */
    font-weight: 300;
    color: rgba(255, 255, 255, 0.9);
    z-index: 1;
    transition: 0.5s;
  }
  
  body .container .cardd .box .content a {
    color: black;
    position: relative;
    display: inline-block;
    padding: 6px 15px;
    background: black;
    border-radius: 5px;
    text-decoration: none;
    margin-top: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    transition: 0.5s;
  }
  
  body .container .cardd .box .content a:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
    background: #fff;
    color: #000;
  }
